.testimonials {
  &__item {
    margin-bottom: 48px;

    &:last-child {
      margin-bottom: 0;
    }

    @include onDesktop {
      margin-bottom: 0;
    }
  }
}

.testimonial {
  &__photo {
    grid-column: 2 / 4;
    width: 100%;
    border-radius: 50%;
    margin-bottom: 28px;

    @include onDesktop {
      margin-bottom: 32px;
    }

    &-container {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      column-gap: 24px;
      transition: transform 300ms;

      &:hover {
        transform: scale(1.1);
      }
    }
  }

  &__name {
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    text-align: center;
    margin: 0 0 4px;
  }

  &__role {
    margin: 0 0 4px;
    font-weight: 400;
    font-size: 14px;
    line-height: 27px;
    text-align: center;

    color: #9fadc2;
  }

  &__about {
    text-align: center;
  }

  &__link {
    @include link;
  }

}
