@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;

  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin onDesktop {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin top-actions {
  display: grid;
  grid-template-columns: 18px 1fr 18px;
  justify-items: center;
  align-items: center;

  @include onDesktop {
    grid-template-columns: 24px 1fr 24px;
  }
}

@mixin link {
  text-decoration: none;
  color: $colorBlue;
}
